<template>
	<view>
		<view class="header" :style="{position:headerPosition,top:headerTop}">
			<view class="target" v-for="(target,index) in orderbyList" @tap="select(index)" :key="index" :class="[target.selected?'on':'']">
				{{target.text}}
				<view v-if="target.orderbyicon" class="icon" :class="target.orderbyicon[target.orderby]"></view>
			</view>
		</view> 
		<!-- 占位 -->
		<view class="place">
		</view>
		<!-- 商品列表 -->
		<view class="xy-product-list">
			<view class="xy-product" :class="'product-'+index" v-for="(goods,index) in goodsList" :key="goods.goods_id" @tap="toGoods(goods)">
				<image class="xy-product-img" mode="widthFix" :src="goods.img"></image>
				<view class="xy-product-name">{{goods.name}}</view>
				<view class="xy-product-info">
					<view class="xy-product-price">{{goods.price}}</view>
					<view class="xy-product-slogan">{{goods.slogan}}</view>
				</view>
			</view>
		</view>
		<view class="xy-loading-text">{{loadingText}}</view>
		
		<!--列表结束-->
		<view :class="[isTouch?'b':'a']" 
		 v-if="top" @tap="toTop">
			<image src="../../static/img/hj.png" style="width: 60upx;height: 60upx;"></image>
		</view>
	</view>
</template>

<script>
import zero from '../../static/img/goods/default.jpg'
	export default {
		data() {
			return {
				top:false,
				isTouch:false,
				defaultImg:zero,
				goodsList: [
					{ goods_id: 0, img: '../../static/img/goods/p1.jpg', name: '牛肉牛肉', price: '168', slogan: '1235人付款' ,show:false},
					{ goods_id: 1, img: '../../static/img/goods/p2.jpg',name: '鸡腿鸡推', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 2, img: '../../static/img/goods/p3.jpg', name: '大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 3, img: '../../static/img/goods/p4.jpg',name: '蜜汁牛排', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 4, img: '../../static/img/goods/p5.jpg', name: '意大利酒心巧克力', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 5, img: '../../static/img/goods/p6.jpg', name: '咖啡伴侣330g', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 6, img: '../../static/img/goods/p7.jpg',name: '肯德基折扣券110元', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 7, img: '../../static/img/goods/p8.jpg', name: '大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹大闸蟹2', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 8, img: '../../static/img/goods/p9.jpg', name: '意大利酒心巧克力芒果味', price: '168', slogan: '1235人付款'  ,show:false},
					{ goods_id: 9, img: '../../static/img/goods/p10.jpg', name: '冷冻大黄鱼', price: '168', slogan: '1235人付款'  ,show:false}
				],
				loadingText:"正在加载...",
				headerTop:"0px",
				headerPosition:"fixed",
				orderbyList:[
					{text:"销量",selected:true,orderbyicon:false,orderby:0},
					{text:"价格",selected:false,orderbyicon:['sheng','jiang'],orderby:0},
					{text:"好评",selected:false,orderbyicon:false,orderby:0}
				],
				orderby:"sheng"
			};
		},
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
			console.log(option.cid); //打印出上个页面传递的参数。
			uni.setNavigationBarTitle({
				title: option.name
			});
			//兼容H5下排序栏位置
			// #ifdef H5
				//定时器方式循环获取高度为止，这么写的原因是onLoad中head未必已经渲染出来。
				let Timer = setInterval(()=>{
					let uniHead = document.getElementsByTagName('uni-page-head');
					if(uniHead.length>0){
						this.headerTop = uniHead[0].offsetHeight+'px';
						clearInterval(Timer);//清除定时器
					}
				},1);
			// #endif
		},
		onReady() {
			this.goodsList.forEach((item,index) =>{
				uni.createIntersectionObserver(this).relativeToViewport({bottom:100}).observe(`.product-${item.goods_id}`, (res) => {
				  if (res.intersectionRatio > 0) {
					  console.log('product-'+index+item.name)
					  this.$set(item,'show',true);
				  }
				});
			});
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			if(e.scrollTop>=0){
				this.headerPosition = "fixed";
				e.scrollTop>1000?this.top=true:this.top=false;
			}else{
				this.headerPosition = "absolute";
			}
		},
		onUnload(){
			console.log('ok')
		     uni.createIntersectionObserver(this).disconnect();
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(()=>{
				this.reload();
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
		onReachBottom(){
			uni.showToast({title: '触发上拉加载'});
			let len = this.goodsList.length;
			if(len>=40){
				this.loadingText="到底了";
				return false;
			}else{
				this.loadingText="正在加载...";
			}
			let end_goods_id = this.goodsList[len-1].goods_id;
			for(let i=1;i<=10;i++){
				let goods_id = end_goods_id+i;
				let p = { goods_id: goods_id, img: '../../static/img/goods/p'+(goods_id%10==0?10:goods_id%10)+'.jpg', name: '商品名称商品名称商品名称商品名称商品名称', price: '￥168', slogan:'1235人付款' };
				this.goodsList.push(p);
			}
		},
		methods:{
			toTop(){//点击回到顶部
				this.isTouch = true;
				uni.pageScrollTo({
					scrollTop: 0,
					duration: 300
				});
				this.isTouch = false;
			},
			reload(){
				console.log("reload");
				let tmpArr = []
				this.goodsList = [];
				let end_goods_id = 0;
				for(let i=1;i<=10;i++){
					let goods_id = end_goods_id+i;
					let p = { goods_id: goods_id, img: '../../static/img/goods/p'+(goods_id%10==0?10:goods_id%10)+'.jpg', name: '商品名称商品名称商品名称商品名称商品名称', price: '￥168', slogan:'1235人付款' };
					this.goodsList.push(p);
				}
			},
			//商品跳转
			toGoods(goods){
				uni.navigateTo({
					url: '../../goodPage/goods/goods?cid='+goods.goods_id+'&&name='+goods.name+'&&img='+goods.img+'&&price='+goods.price
				});
			},
			//排序类型
			select(index){
				let tmpTis = this.orderbyList[index].text+"排序 "
				if(this.orderbyList[index].orderbyicon){
					let type = this.orderbyList[index].orderby==0?'升序':'降序';
					if(this.orderbyList[index].selected){
						type = this.orderbyList[index].orderby==0?'降序':'升序';
						this.orderbyList[index].orderby = this.orderbyList[index].orderby==0?1:0;
					}
					tmpTis+=type
				}
				this.orderbyList[index].selected = true;
				let len = this.orderbyList.length;
				for(let i=0;i<len;i++){
					if(i!=index){
						this.orderbyList[i].selected = false;
					}
				}
				uni.showToast({title:tmpTis,icon:"none"});
			}
		}
		
	}
</script>

<style lang="scss">
	@font-face {font-family:"HMfont-home";src:url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');}
	.icon {
		font-family:"HMfont-home" !important;
		font-size:26upx;
		font-style:normal;
		&.sheng {
			&:before{content:"\e737";}
		}
		&.jiang {
			&:before{content:"\e736";}
		}
		
	}
	.header{
		width: 92%;
		padding: 0 4%;
		height: 79upx;
		display: flex;
		justify-content: space-around;
		align-items: flex-end;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #fff;
		border-bottom: solid 1upx #eee;
		.target{
			width: 20%;
			height: 60upx;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 28upx;
			margin-bottom: -2upx;
			color: #aaa;
			&.on{
				color: #555;
				border-bottom: 4upx solid #8bbce7;
				font-weight: 600;
				font-size: 30upx;
			}
			
			
		}
	}
.place{
		
		background-color: #ffffff;
		height: 100upx;

	}
.input-box {
	width: 100%;
	height: 65upx;
	background-color: #f5f5f5;
	border-radius: 30upx;
	position: relative;
	display: flex;
	align-items: center;
	.icon {
		display: flex;
		align-items: center;
		position: absolute;
		top: 0;
		right: 0;
		width: 60upx;
		height: 60upx;
		font-size: 34upx;
		color: #c0c0c0;
	}
	input {
		padding-left: 28upx;
		height: 28upx;
		font-size: 28upx;
	}
}
	.a{
		border: 1px solid #8BBCE7;
		color:#8BBCE7;
		border-radius: 100%;
		position: fixed;
		bottom: 50upx;right: 50upx;
		z-index: 2;
		background: #fff;
		padding: 5upx;
	}

	@keyframes spin {
	    0% {
			opacity: 1;
			bottom:50upx;
		}
		50% {
			opacity: 0.6;
			bottom:100upx;
		}
		100% {
			opacity: 0;
			bottom:150upx;
		}
	}
	a:hover{
		animation: spin 1.2s 1;
	}
	.b{
		border: 1px solid #8BBCE7;
		color:#8BBCE7;
		border-radius: 100%;
		position: fixed;
		bottom: 50upx;right: 50upx;
		animation: spin 1.2s 1;
		z-index: 2;
		background: #fff;
		padding: 5upx;
	}
</style>
